  @import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');


.App{
  background-color: #eeeeee;
}

/* header */

.navigation{
  font-family: 'Architects Daughter', cursive;
  font-size: 1.5rem;
  position: relative;
 
}

/* home */

h1{
  color: #222831;
  font-family: 'Comfortaa', cursive;
  font-size: 4rem;
}
p{
  display: flex;
  justify-content: center;
  color: #525252;
  font-family: 'Comfortaa', cursive;

}

h2{
  color: #525252;
  font-family: 'Comfortaa', cursive;
  font-size: 1rem;
}
.side{
  height: 100%;
  width: 100%;

}

/* type writer */
.typewriter{
  overflow: hidden;
  border-right: 2px solid;
  white-Space: nowrap;
  margin: 0 auto;
  user-select: none;
  
  

  animation: typing 2.1s steps(30 , end),
            blink-caret 1s step-end infinite;
}

@keyframes typing{
  from{
    width: 0;
  }to{
    width: 100%;
  }
}

@keyframes blink-caret{
  50%{
    border-color: transparent;
  }
}
/* services */

.service{
  float: left;
  width: 30vh;
}
.service2{
  float:right;
  width: 30vh;
}
.serviceContainer{
  margin: 0vw 10vw 0vw 10vw;
}

.logo{
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 600px) {
  .App{
    background-color: #eeeeee;
  }
  .service{
    width: 80vw;
  }
  .service2{
    width: 80vw;
  }
  .serviceContainer{
    margin: 0vw 0vw 0vw 0vw;
  }
  .logoheadline{
    padding-top: 1vh;
  }
  

}

